import React from "react";
import { Panel } from '@vkontakte/vkui';
import { PanelHeader } from '@vkontakte/vkui';
import { PanelHeaderButton } from '@vkontakte/vkui';
import { PanelHeaderBack } from '@vkontakte/vkui';
import { Group } from '@vkontakte/vkui';
import { Div } from '@vkontakte/vkui';
import { Title } from '@vkontakte/vkui';


const DateReg = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader
            addon={<PanelHeaderButton onClick={go} data-to="Main">Назад</PanelHeaderButton>}
            left={<PanelHeaderBack onClick={go} data-to="Main" />}
        >Дата регистрации</PanelHeader>
        <Group>
            < Div >
                <div style={{
                    backgroundImage: "url('https://sun9-42.userapi.com/hbCbZ3XKoB8s6cR6D2WPRCb2bUDGx2YY1ejv4w/6ht81wQbY7Y.jpg')",
                    backgroundRepeat: "no-repeat",
                    backgroundSize: "cover",
                    backgroundPosition: "center",
                    height: 200,
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    paddingBottom: "6px",
                    borderRadius: 12
                }}>
                    <Title level="1" weight="heavy" id="DateRegText" style={{
                        color: "#fff",
                        textShadow: "4px 4px 5px #000, -4px 4px 5px #000,-4px -4px 5px #000, 4px -4px 5px #000"
                    }}>Ты шо, дурак шоль!?</Title>
                </div>
            </Div>
        </Group>
    </Panel>

);

export default DateReg;